import { useDispatch } from "react-redux";
import {
  setCAAllPlants,
  setCAHeaders,
  setCAItemDetails,
  setHeaders,
  setIsMFGCA,
  setLoading,
  setObselenceObjectData,
  setParentDetailsLoading,
  setProductChildren,
  setProposedChanges,
  setSpecDocument,
  setCAName,
  setCAData
} from "../store/droppedObjectSlice";
import { MSG_FETCH_CSRF_HEADERS_FAILED } from "../utils/toastMessages";
import useToast from "./useToast";
import { fetchCsrfToken } from "../services/api/PlantAssignment/fetchCsrfService";
import { callEnoviaWebService } from "../utils/helpers";
import { useCallback, useEffect } from "react";
import { getUserGroupCollab } from "../services/api/PlantAssignment/userGroupService";
import { getAllPlants } from "../services/api/PlantAssignment/allPlantSevice";
import {
  getAssignedClasses,
  getItemDetails,
  getLatestRevision,
  getScopedItms,
} from "../pages/plantAssignment/dataHelpers";
import { fetchAssignedPlants } from "../services/api/PlantAssignment/assignedPlantService";
import { fetchProductChildren } from "../services/api/PlantAssignment/plantChildrenService";
import { fetchCADetails } from "../services/api/PlantAssignment/CADetailService";

const useObsolescenceWidget = () => {
  const { showErrorToast } = useToast();
  const dispatch = useDispatch();
  const ENOVIA_BASE_URL = process.env.REACT_APP_ENOVIA_BASE_URL;
  let email = window.widget.getValue("email");

  const handleObsolescenceWidget = async (collabSpace, objectId, type, relativePath) => {
    if (!collabSpace || !objectId || !type || !relativePath) {
      console.error("[handleObsolescenceWidget] âŒ Missing required argument(s):", {
        collabSpace,
        objectId,
        type,
        relativePath,
      });
      return;
    }

    try {
      dispatch(setObselenceObjectData({ parentObjectData: [] }));
      let tableOfArrayData = [];
      dispatch(setParentDetailsLoading(true));

      const headers = await fetchCsrfToken();
      console.log("headerssss", headers);

      if (!headers) {
        showErrorToast(MSG_FETCH_CSRF_HEADERS_FAILED);
        return;
      }

      let ChildObject = await locateApi(ENOVIA_BASE_URL, objectId, type, relativePath, headers);
      console.log("SpecDetails Response:", ChildObject);

      if (ChildObject.status && ChildObject.output) {
        let member = ChildObject?.output?.member?.[0]?.["dseng:EngInstance"]?.member;
        console.log("ðŸ” EngInstance members:", member);

        if (member?.length > 0) {
          let url = member.map(
            (items) => items.parentObject?.source + items.parentObject?.relativePath
          );
          let revisionStateData = [];

          const promises = url.map(async (apiPath) => {
            const revisionState = await callEnoviaWebService("GET", apiPath, "", headers);
            if (revisionState) {
              revisionStateData = [...revisionStateData, ...revisionState?.output?.member];
            }
          });

          await Promise.all(promises);
          console.log("revisionStateData", revisionStateData);
          dispatch(setObselenceObjectData({ parentObjectData: revisionStateData }));
        }

        if (type === "Change Action") {
          let isMFGCA = false;
          let ItemDetails = [];

          const fetchChangeActionData = async () => {
            const CAURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/dslc/changeaction/${objectId}?%24fields=proposedChanges,realizedChanges`;
            const response = await callEnoviaWebService("GET", CAURL, "", headers);

            if (response.status && response.output) {
              for (let change of response?.output?.proposedChanges || []) {
                const url = change.where.source + change.where.relativePath;
                const responseofTable = await callEnoviaWebService("GET", url, "", headers);
                if (responseofTable.status) {
                  tableOfArrayData.push({
                    ...responseofTable?.output?.member?.[0],
                    changeIntent: change?.whats,
                  });
                }
              }
              console.log("Table of Array Data:", tableOfArrayData);
            }
          };

          const CAAllPlantsData = async () => {
            const allPlants = await getAllPlants([collabSpace], headers, objectId);
            dispatch(setCAAllPlants(allPlants));
          };

          await Promise.all([fetchChangeActionData(), CAAllPlantsData()]);
          dispatch(setParentDetailsLoading(false));
          dispatch(setCAItemDetails(ItemDetails));
          dispatch(setIsMFGCA(isMFGCA));
          dispatch(setCAHeaders(headers));
          dispatch(setObselenceObjectData({ parentObjectData: tableOfArrayData }));
        } else {
          const userGroupCollab = await getUserGroupCollab(headers, objectId, email);
          let allCollabSpaces = [...userGroupCollab, collabSpace];

          let allPlants = [];
          if (allCollabSpaces.length > 0) {
            allPlants = await getAllPlants(allCollabSpaces, headers, objectId);
          }

          if (allPlants.length > 0) {
            const plants = await fetchAssignedPlants(allPlants, headers, objectId);
            if (plants.success) {
              dispatch(setObselenceObjectData(plants.data.plantData));
              dispatch(setHeaders(headers));
            }
          }

          let getProductChildren = {};
          if (type === "Raw_Material") {
            getProductChildren = { success: true, data: [] };
          } else {
            getProductChildren = await fetchProductChildren(headers, objectId, type);
          }

          if (getProductChildren.success) {
            dispatch(setProductChildren(getProductChildren.data));
          }

          const state = null;
          const getCaDetails = await fetchCADetails(headers, objectId, state);

          if (getCaDetails.success) {
            dispatch(setCAName(getCaDetails.data));
            dispatch(setProposedChanges(getCaDetails.proposedChanges));
            dispatch(setCAData(getCaDetails.CAData));
          }
        }
      }
    } catch (error) {
      console.error("[useObsolescenceWidget] Error:", error);
      throw error;
    } finally {
      dispatch(setParentDetailsLoading(false));
    }
  };

  const obsoleteStateChecking = async (tableData, droppedObjectData) => {
    if (!tableData) {
      showErrorToast("Items Not Found");
      return;
    }

    console.log("droppedObjectData in custom hook", droppedObjectData);
    let itemGroupId = droppedObjectData?.initialDraggedData?.data;
    let objectId = itemGroupId?.items?.[0]?.objectId;
    console.log("card Data object Id", objectId);

    try {
      dispatch(setLoading(true));
      const headers = await fetchCsrfToken();
      console.log("ðŸ” CSRF headers:", headers);

      if (!headers) {
        showErrorToast(MSG_FETCH_CSRF_HEADERS_FAILED);
        return;
      }

      let locateParentObjectsArray = [];
      let finalObsoleteStatusArray = [];

      for (let i = 0; i < tableData.length; i++) {
        const item = tableData[i];
        console.log("ðŸ“¦ Checking item:", item);

        let locateApiResponse = await locateApi(
          ENOVIA_BASE_URL,
          item.id,
          item.type,
          `/resources/v1/modeler/dseng/dseng:EngItem/${item.id}`,
          headers
        );

        console.log("ðŸ“ Locate API Response:", locateApiResponse);

        if (locateApiResponse?.status) {
          const engInstances = locateApiResponse?.output?.member?.[0] || {};
          const locateParentObject =
            engInstances["dseng:EngInstance"]?.member || [];
          locateParentObjectsArray.push(locateParentObject);
        }
      }

      console.log("ðŸ—‚ Locate Parent Objects Array:", locateParentObjectsArray);

      if (locateParentObjectsArray.length > 0) {
        for (let j = 0; j < locateParentObjectsArray.length; j++) {
          const parentMembers = locateParentObjectsArray[j];

          if (parentMembers?.length > 0) {
            await Promise.all(
              parentMembers.map(async (item) => {
                let revisionState = await callEnoviaWebService(
                  "GET",
                  `${item?.parentObject?.source}${item?.parentObject?.relativePath}`,
                  "",
                  headers
                );

                console.log("ðŸ”„ Revision State Response:", revisionState);

                if (revisionState?.status) {
                  const obsoleteStateData = revisionState?.output?.member?.[0];
                  finalObsoleteStatusArray.push(obsoleteStateData);
                }
              })
            );
          }
        }
      }

      console.log("âœ… Final Obsolete Status Array:", finalObsoleteStatusArray);
      const hasObsolete = finalObsoleteStatusArray.every(
        (item) => item.state === "OBSOLETE"
      );
      console.log("ðŸ“Œ All items OBSOLETE:", hasObsolete);

      if (hasObsolete) {
        const currentState = droppedObjectData?.cardData?.["Maturity State"];
        console.log("ðŸ” Current maturity state:", currentState);

        if (currentState === "In Work") {
          const response = await stateChange(
            objectId,
            "In Approval",
            ENOVIA_BASE_URL,
            headers,
            showErrorToast
          );
          console.log("ðŸš€ State change to In Approval:", response);
        } else if (currentState === "Draft") {
          const response1 = await stateChange(
            objectId,
            "In Work",
            ENOVIA_BASE_URL,
            headers,
            showErrorToast
          );
          console.log("ðŸ§ª State change to In Work response:", response1);
          console.log("ðŸš€ State change to In Work:", response1);

          if (response1?.status) {
            const response2 = await stateChange(
              objectId,
              "In Approval",
              ENOVIA_BASE_URL,
              headers,
              showErrorToast
            );
            console.log("ðŸš€ State change to In Approval:", response2);
          } else {
            console.error("âŒ Failed to update to 'In Work'");
          }
        }
      } else {
        showErrorToast("All Parent Items Should be Obsolete");
      }
    } catch (err) {
      console.error("ðŸ”¥ Error during obsolete state check:", err);
      showErrorToast("Error in Obsolence: " + (err?.message || "Unknown Error"));
    } finally {
      dispatch(setLoading(false));
    }
  };

  return { handleObsolescenceWidget, obsoleteStateChecking };
};

export default useObsolescenceWidget;

