async function locateApi(ENOVIA_BASE_URL, objectId, type, relativePath, headers) {
  relativePath = relativePath || `/resources/v1/modeler/dseng/dseng:EngItem/${objectId}`;

  const baseURL = `${ENOVIA_BASE_URL}/resources/v1/modeler/dseng/dseng:EngItem/locate`;
  const body = {
    referencedObjects: [
      {
        source: ENOVIA_BASE_URL,
        type,
        identifier: objectId,
        relativePath,
      },
    ],
  };

  console.log("ðŸ“¡ Calling locate API with body:", body);

  const ChildObject = await callEnoviaWebService("POST", baseURL, body, headers);

  if (!ChildObject.status) {
    console.error("âŒ locateApi failed:", ChildObject);
  }

  return ChildObject;
}


async function stateChange(objectId, nextState, ENOVIA_BASE_URL, headers, showErrorToast) {
  try {
    let body = {
      data: [{ id: objectId, nextState: nextState }],
    };
    let maturitySatate = await maturityChangeApi(ENOVIA_BASE_URL, body, headers);
    console.log("âœ… maturitySatate in custom hook", maturitySatate);
    return maturitySatate;
  } catch (err) {
    console.error("âŒ Error updating state:", err);
    showErrorToast("Error Updating State");
  }
}

async function maturityChangeApi(ENOVIA_BASE_URL, body, headers) {
  let baseURL = `${ENOVIA_BASE_URL}/resources/v1/modeler/dslc/maturity/changeState`;
  return await callEnoviaWebService("POST", baseURL, body, headers);
}
